<template lang='jade'>
  .datepicker-page.am-container
    h1 DatePicker
    h2 Basic
    am-date-picker(v-model='date1')
    pre
      | am-date-picker(v-model='date1')
    h2 Placeholder
    am-date-picker(v-model='date2', placeholder='Select Date')
    pre
      | am-date-picker(v-model='date2', placeholder='Select Date')
    h2 Limit
    am-date-picker(v-model='date3', max='2017-06-01', min='2016-06-1')
    pre
      | am-date-picker(v-model='date3', max='2017-06-01', min='2016-06-1')
    h2 Color
    am-date-picker(type='success', v-model='date4')
    am-date-picker(type='warning', v-model='date4')
    am-date-picker(type='danger', v-model='date4')
    pre
      | am-date-picker(type='success', v-model='date4')
      | am-date-picker(type='warning', v-model='date4')
      | am-date-picker(type='danger', v-model='date4')
    h2 Format
    am-date-picker(v-model='date5', format='yyyymd')
    pre
      | am-date-picker(v-model='date5', format='yyyymd')
</template>

<script>
import AmDatePicker from 'src/DatePicker'

export default {
  components: {
    AmDatePicker
  },
  data () {
    return {
      date1: '1970-01-01',
      date2: '',
      date3: '2017-01-01',
      date4: '1970-01-01',
      date5: '197011'
    }
  }
}
</script>

<style lang='less'>
.am-date-picker + .am-date-picker {
  margin-top: 1rem;
}
</style>



